export default {
  __name: 'BaseButton',
  props: {
    size: {
      type: String,
      default: "lg",
      validator: value => ["sm", "md", "lg"].includes(value)
    }
  },
  setup(__props, {
    expose
  }) {
    expose();
    const __returned__ = {};
    Object.defineProperty(__returned__, '__isScriptSetup', {
      enumerable: false,
      value: true
    });
    return __returned__;
  }
};