import { auth as AuthRequest } from "@/Shared/Api/Auth.api";
import { useUserStore } from "@/Shared/State/useUserStore/useUserStore";
import { ref } from "vue";
import BaseButton from "../../BaseButton/BaseButton.vue";
import BaseInput from "../../BaseInput/BaseInput.vue";
export default {
  __name: 'TheAuth',
  setup(__props, {
    expose
  }) {
    expose();
    const userState = useUserStore();
    const form = ref(null);
    const errors = ref([]);
    const auth = () => AuthRequest(new FormData(form.value)).then(response => {
      if (response.data.success) {
        userState.authenticate(response.data.data);
      } else {
        console.log(response.data);
        errors.value = response.data.errors;
      }
    });
    const __returned__ = {
      userState,
      form,
      errors,
      auth,
      get AuthRequest() {
        return AuthRequest;
      },
      get useUserStore() {
        return useUserStore;
      },
      ref,
      BaseButton,
      BaseInput
    };
    Object.defineProperty(__returned__, '__isScriptSetup', {
      enumerable: false,
      value: true
    });
    return __returned__;
  }
};